var hours = document.querySelector(".hours input");
var minuts = document.querySelector(".minuts input");
var seconds = document.querySelector(".seconds input");
var timeId;
var isStartTimer = false;



var start = document.querySelector(".timer-buttons .green-btn");
var stop = document.querySelector(".timer-buttons .red-btn");
var pause = document.querySelector(".timer-buttons .yellow-btn")

var inputs = document.getElementsByTagName('input');


start.addEventListener('click', function () {
var secondsValue = +seconds.value;
var minutsValue = +minuts.value;
var hoursValue = +hours.value;

    
//Реализация секунд
   timeId = setInterval(function() {

    //Перерасчёт секунд в минуты если вдруг пользователь введёт цифру > 59;
        if (secondsValue > 59) {
            minutsValue = Math.floor(secondsValue / 60);
            secondsValue = secondsValue % 60;
            minutsValue < 10 ? minuts.value = '0' + minutsValue : minuts.value = minutsValue;
        }

        //Реализация хода раздела Часы.
        if (hoursValue && !minutsValue && !secondsValue) {
            --hoursValue;
            minutsValue = 60;
        }

        hoursValue < 10 ? hours.value = '0' + hoursValue : hours.value = hoursValue;


    //Это условие проверяет равны ли секунды 0 или же запущен ли таймер только с минутами.
        if (minutsValue && !secondsValue) {
            secondsValue = 60;
        }
    
    /* Это условие проверяет на наличие какого либо значения у секунд минут или часов. 
    Если же его нет, останавливает таймер. 
    В другом случае уменьшает секуды.
    */

        if (!secondsValue && !minutsValue && !hoursValue) {
            isStartTimer ? document.getElementById('audiofile').play() : clearInterval(timeId);
            isStartTimer = false;
        } else if(secondsValue){
            console.log(--secondsValue);
            isStartTimer = true;
        }

        //Простейшее проверка на значение секунд, если секунды меньше 10 тогда в перёд добавляем ноль.
        secondsValue < 10 ? seconds.value = '0' + secondsValue : seconds.value = secondsValue;

//Реализация минут

        //Проверка секундного значения на 59. 
        if (secondsValue == 59) {
            --minutsValue;
            minutsValue < 10 ? minuts.value = '0' + minutsValue : minuts.value = minutsValue;
        } 
//Реализация часов



//Реализация интерфейсной части. 

if (secondsValue || minutsValue || hoursValue) {
    for (var i = 0; i < inputs.length; i++) {
        inputs[i].setAttribute('readonly', null);
        inputs[i].style.cursor = 'default';
    }
}
  
    }, 1000)
});

stop.addEventListener('click', function () {
    clearTimeout(timeId);
    document.getElementById('audiofile').pause();
    document.getElementById('audiofile').currentTime = 0;
    seconds.value = minuts.value = hours.value = "00";

    
for (var i = 0; i < inputs.length; i++) {
    inputs[i].removeAttribute('readonly');
    inputs[i].style.cursor = 'text';
}
});

pause.addEventListener('click', function() {
    clearTimeout(timeId);
    document.getElementById('audiofile').pause();
    for (var i = 0; i < inputs.length; i++) {
        inputs[i].removeAttribute('readonly');
        inputs[i].style.cursor = 'text';
    }
});